@keyframes slideIn {
    0% {
        left: -100%;
        opacity: 0;
    }
    100% {
        left: 0;
        opacity: 1;
    } 
}

.quote {
    margin-top: 10%;
}

.quote>span {
    font-size: 200%;
    color: #C9AD98;
    white-space: pre;
    text-shadow: 1px 1px 1px #8c796a;
}

.socials {
    font-size: 200%;
    position: relative;
    margin-top: 25%;
}

.socials i {
    color: #C9AD98;
    margin: 10px;
    text-shadow: 1px 1px 1px #8c796a;
}
.socials i:hover {
    animation: scaleUp 1s;
    animation-fill-mode: backwards; 
}

@keyframes scaleUp {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}